<template>
  <div class="bottom-bar">
    <div class="check-content">
      <check-button :is-checked="isSelectAll" class="check-button" @click.native="selectAll"></check-button>
      <span>全选</span>
    </div>
    <div class="price">合计：{{totalPrice}}</div>
    <div class="calculate" @click="calculate">去计算({{checkLength}})</div>
  </div>
</template>

<script>
import CheckButton from "components/content/checkButton/CheckButton";
import { mapGetters } from "vuex";
export default {
  name: "CartBottomBar",
  components: {
    CheckButton
  },
  computed: {
    ...mapGetters(["cartList"]),
    totalPrice() {
      return (
        "¥" +
        this.cartList
          .filter(item => {
            return item.checked;
          })
          .reduce((preValue, item) => {
            return preValue + item.price * item.count;
          }, 0)
          .toFixed(2)
      );
    },
    checkLength() {
      return this.cartList.filter(item => item.checked).length;
    },
    isSelectAll() {
      // return this.cartList.filter(item=>{
      //   return item.checked
      // }).length==this.cartList.length
      // return !(this.cartList.filter(item=>!item.checked).length)
      // return !(this.cartList.length!=this.checkLength)
      if (this.cartList.length === 0) return false;
      return !(this.cartList.length != this.checkLength);
    }
  },
  methods: {
    selectAll() {
      if (this.isSelectAll) {
         this.$store.commit('selectAllFalse')
      } else {
         this.$store.commit('selectAllTrue')
      }
    },
    calculate(){
      if(this.checkLength==0){
        this.$toast.show('请选择购买的商品',2000)
      }

      
    }
  }
};
</script>

<style >
.bottom-bar {
  display: flex;
  position: fixed;
  width: 100%;
  height: 40px;
  background-color: #eee;
  bottom: 50px;
  line-height: 40px;
}
.check-content {
  display: flex;
  align-items: center;
  margin-left: 10px;
  width: 60px;
}
.check-button {
  width: 20px;
  height: 20px;
  line-height: 20px;
  margin-right: 5px;
}

.price {
  margin-left: 20px;
  flex: 1;
}

.calculate {
  width: 90px;
  background-color: var(--color-tint);
  color: #ffffff;
  text-align: center;
}
</style>